.f_button {
	position: relative;
	display: inline-flex;
	align-items: center;
  justify-content: center;
	background: none;
  border: 0 none;

  --font-size: 16px;
  --font-shadow: var(--font-size);
  --text: #042419;
  --shadow-hover: 0 4px 20px -2px #{rgba(#275efe, .5)};
  --background-hover: #095D40;

	font-size: var(--font-size);
	font-weight: 600;
	line-height: 1em;
	color: var(--text);
  height: 48px;
  transition: all .5s;
  background: #FAFCFC;
  border: 1px solid #E0F0EC;
  border-radius: 100px;
  overflow: hidden;
  z-index: 0;
  min-width: 190px;
  box-shadow: 0px 12px 18px -12px rgba(4, 36, 25, 0.07);

  div {
      display: flex;
      overflow: hidden;
      text-shadow: 0 var(--font-shadow) 0 #fff;
      span {
        display: block;
        backface-visibility: hidden;
        font-style: normal;
        transition: transform .3s ease;
        transform: translateY(var(--m)) translateZ(0);
        $i: 1;
        @while $i < 12 {
          &:nth-child(#{$i}) {
            transition-delay: $i / 20 + s;
          }
          $i: $i + 1;
        }
      }
  }
  &:focus {
    color: inherit;
  }
  &:hover {
    --y: -4px;
    --shadow: var(--shadow-hover);
    background: var(--background-hover);
    border-color: var(--background-hover);

    span {
      --m: calc(var(--font-size) * -1);
    }
  }

  &:active {
    background: #FAFCFC;
    border-color: #E0F0EC;
    //color: var(--text);

    div {
      text-shadow: 0 var(--font-shadow) 0 var(--text);
    }
  }

  &.black {
    background: #042419;
    color: #fff;
    border-color: #095E40;

    &:hover {
      background: #095E40;
      border-color: #095E40;
    }

    &:focus {
      color: #fff;
    }

    &:active {
      background: #042419;
      color: #fff;
      border-color: #095E40;

      div {
        text-shadow: 0 var(--font-shadow) 0 #fff;
      }
    }
  }

  &:disabled,
  &.disabled {
    pointer-events: none;
    background: #D1D1D1;
    border-color: #D1D1D1;
    color: #fff;
  }
}

.f_group {
  position: relative;
  margin-top: 24px;

  @media (max-width: $mobile) {
    margin-top: 24px;
  }

  &:first-child {
    margin-top: 0;
  }
}

.f_label {
  display: flex;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 4px;
  font-weight: 500;
  color: #2E3F3A;
}

.f_area,
.f_input {
	width: 100%;
	display: block;
  padding: 0 14px;
	font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  height: 46px;
	color: #2E3F3A;
  background: #FFFFFF;
  border: 1px solid #C2D2CE;
  box-shadow: 0px 0px 0px 2px transparent;
  border-radius: 8px;
  transition: all .3s;

  &:focus,
	&:active,
  &:hover {
    box-shadow: 0px 0px 0px 2px #E6FAF2;
  }

	&::-webkit-input-placeholder {
		color: #979797;
	}
	&::-moz-placeholder {
	  color: #979797;
	}
	&:-ms-input-placeholder {
	  color: #979797;
	}
	&:-moz-placeholder {
	  color: #979797;
	}
}

.f_area {
  padding: 10px 14px;
  min-height: 88px;
  resize: none;
}

.error {
  .f_input {
    border-color: #C20707;
    color: #C20707;
  }

  .error-msg {
    display: inline-flex;
    font-size: 14px;
    color: #C20707;
    line-height: 20px;
    margin-top: 6px;
  }
}

.show-menu {
  .hamburger {
    align-items: center;
    span {
      background: #042419;
    }
    .span-1 {
      transform: rotate(45deg) translate(5px);
    }

    .span-2 {
      width: 21px;
      transform: rotate(495deg) translate(0, -1px);
    }

    .span-3 {
      transform: rotate(45deg);
      opacity: 0;
    }
  }
}

.hamburger {
  display: none;

  @media (max-width: $tablet) {
    display: inline-flex;
    width: 28px;
    height: 28px;
    border: none;
    cursor: pointer;
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    background: none;
    z-index: 1;
    margin-left: 48px;
  }

  @media (max-width: $mobile) {
    margin-left: 19px;
  }

  span {
    width: 21px;
    height: 2px;
    margin-bottom: 4px;
    background: #101828;
    transition: all 0.5s cubic-bezier(0.08, 0.81, 0.87, 0.71);
    border-radius: 100px;

    &.span-1 {
      transform-origin: 5px 0px;
    }

    &.span-3 {
      transform-origin: bottom right;
      margin-bottom: 0;
      width: 12px;
    }
  }
}

.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(266.71deg, #443EE6 3.38%, #D74FBA 99.24%);
  border-radius: 80px;
  padding: 0 12px;
  min-height: 32px;
  font-size: 16px;
  line-height: 24px;
  color: #fff;
  font-weight: 600;
  letter-spacing: -0.02em;
  margin-bottom: 8px;

  @media (max-width: $mobile) {
    font-size: 12px;
    line-height: 24px;
  }
}