.modal {
  position: fixed;
  z-index: 10000;
  /* 1 */
  top: 0;
  left: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  overflow-y: auto;
}

.modal.is-visible {
  visibility: visible;
}

.modal-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(1, 13, 8, 0.8);
  visibility: hidden;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.modal.is-visible .modal-overlay {
  opacity: 1;
  visibility: visible;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}

.modal-wrapper {
  position: relative;
  z-index: 9999;
  width: -webkit-calc(100% - 16px);
  width: calc(100% - 16px);
  background-color: #fff;
  padding: 48px 96px;
  border-radius: 12px;
  max-width: 560px;
  height: auto;
  display: flex;
  flex-direction: column;

  @media (max-width: $mobile) {
    padding: 40px 16px;
  }
}

.modal-transition {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
}

.modal.is-visible .modal-transition {
  opacity: 1;
}

.modal-close {
  position: absolute;
  top: 24px;
  right: 24px;
  border: 0;
  background: url(../img/tk-close-icon.svg) no-repeat 50% 50%;
  width: 32px;
  height: 32px;
  border-radius: 100px;
  padding: 0;
  -webkit-transition: all .3s;
  transition: all .3s;

  @media (max-width: $mobile) {
    top: 16px;
    right: 16px;
  }
}

.modal-close:hover {
  background-color: #F0F5F0;
}

.modal-heading {
  display: flex;
  font-weight: 700;
  font-size: 44px;
  line-height: 48px;
  text-align: center;
  justify-content: center;
  color: #042419;
  margin-bottom: 24px;

  @media (max-width: $mobile) {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 16px;
  }
}

.modal-body {
  -webkit-box-flex: 0;
  -webkit-flex: 0 100%;
  -ms-flex: 0 100%;
  flex: 0 100%;
}

.modal-contact-form {
  .f_group {
    &:last-child {
      margin-top: 32px;

      @media (max-width: $mobile) {
        margin-top: 32px;
      }

      .f_button {
        width: 100%;
      }
    }
  }
}

.modal-thanks {
  position: relative;
  text-align: center;
  max-width: 560px;

  .modal-heading {
    margin-bottom: 32px;
    justify-content: center;

    @media (max-width: $mobile) {
      margin-bottom: 24px;
    }
  }

  object {
    margin-bottom: 24px;
  }

  p {
    margin: 0 0 43px;
    font-size: 16px;
    line-height: 24px;
    color: #2E3F3A;

    @media (max-width: $mobile) {
      margin-bottom: 39px;
    }
  }

  .f_button {
    width: 100%;
  }
}
