.show-menu {
  overflow: hidden;
  &:before {
    transition-delay: 0.3s;
    opacity: 1;
    visibility: visible;
  }

  .header {
    .wrapper {
      .nav-menu {
        @media (max-width: $tablet) {
          //transform: translateX(0);
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 72px;
  z-index: 11;
  transition: all 600ms ease;

  @media (max-width: $tablet) {
    height: 64px;
  }

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    transition: all 600ms ease;

    @media (max-width: $tablet) {
      height: 64px;
    }

    .logo {
      display: inline-flex;
      background: url(../img/tk-logo.svg) 0 0 no-repeat;
      width: 128px;
      height: 24px;
      background-size: 100% auto;
      margin-right: 64px;

      @media (max-width: $mobile) {
        width: 106px;
        height: 20px;
        margin-right: auto;
      }
    }

    .nav-menu {
      margin-right: auto;

      @media (max-width: $tablet) {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        width: 464px;
        padding: 200px 0 0 64px;
        z-index: 0;
        background: #E6FAF2;
        transition: all 1s;
        opacity: 0;
        visibility: hidden;
      }

      @media (max-width: $mobile) {
        width: 288px;
        padding: 120px 0 0 24px;
        background-size: auto 100%;
      }

      &:before {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 464px;
        content: "";
        background: rgba(1, 13, 8, 0.8);
        display: none;

        @media (max-width: $tablet) {
          display: block;
        }

        @media (max-width: $mobile) {
          right: 288px;
        }
      }

      ul {
        @include reset-list;
        display: flex;
        align-items: center;
        margin: 0 -20px;

        @media (max-width: $tablet) {
          flex-direction: column;
          align-items: flex-start;
          height: 100%;
          margin: 0;
          counter-reset: nav-number;
        }

        li {
          margin: 0 20px;

          @media (max-width: $tablet) {
            display: flex;
            width: 100%;
            margin: 32px 0 0;
          }

          &:first-child {
            @media (max-width: $tablet) {
              margin-top: 0;
            }
          }

          &:before {
            counter-increment: nav-number;
            content: "0" counter(nav-number);
            color: #042419;
            font-size: 24px;
            line-height: 32px;
            font-weight: 600;
            margin-right: 16px;
            display: none;

            @media (max-width: $tablet) {
              display: flex;
            }

            @media (max-width: $mobile) {
              font-size: 22px;
              line-height: 30px;
              margin-right: 8px;
            }
          }
        }

        a {
          position: relative;
          display: inline-flex;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          color: #082019;
          transition: all .3s;

          @media (max-width: $tablet) {
            display: flex;
            color: #042419;
            font-size: 24px;
            line-height: 32px;
            font-weight: 600;
          }

          @media (max-width: $mobile) {
            font-size: 22px;
            line-height: 30px;
          }

          &:before {
            position: absolute;
            bottom: -6px;
            left: -3px;
            right: -3px;
            content: "";
            height: 2px;
            background: #082019;
            border-radius: 12px 12px 0px 0px;
            transition: all .3s;
            opacity: 0;
          }

          &:focus,
          &:hover {
            color: #6B837C;
          }

          &.active {
            &:before {
              opacity: 1;
              bottom: -2px;
            }
          }
        }
      }
    }

    .f_button {
      margin-left: 56px;
      min-width: 120px;
      height: 40px;

      &.black {
        margin-left: 16px;

        @media (max-width: $mobile) {
          min-width: 88px;
          margin-left: 6px;
        }
      }

      @media (max-width: $tablet) {
        margin-left: auto;
      }

      @media (max-width: $mobile) {
        min-width: 64px;
        margin-left: 0;
        font-size: 10px;
        height: 26px;
      }
    }
  }
}