.scrolling {
  .header {
    height: 60px;
    background: #E6FAF2;
    box-shadow: 0px 12px 18px -12px rgba(4, 36, 25, 0.07);

    @media (max-width: $mobile) {
      height: 50px;
    }

    .wrapper {
      height: 60px;

      @media (max-width: $mobile) {
        height: 50px;
      }
    }
  }

}

.main-title {
  display: flex;
  justify-content: center;
  font-style: normal;
  font-size: 44px;
  line-height: 48px;
  text-align: center;
  color: #042419;
  font-weight: bold;

  @media (max-width: $mobile) {
    font-size: 36px;
    line-height: 44px;
  }
}

.main-section {
  position: relative;
  background: #E6FAF2;
  padding-top: 176px;
  margin-bottom: 68px;

  @media (max-width: $tablet) {
    padding-top: 110px;
  }

  @media (max-width: $mobile) {
    padding-top: 90px;
    margin-bottom: 48px;
  }

  &:before {
    position: absolute;
    bottom: -67px;
    left: 0;
    right: 0;
    content: "";
    height: 68px;
    background: url(../img/tk-corner-bg.jpg) 50% 100% no-repeat;
    z-index: -1;
    -webkit-background-size: cover;
    background-size: cover;

    @media (max-width: $mobile) {
      height: 48px;
      bottom: -47px;
      background-color: #E6FAF2;
      background-size: 100% auto;

    }
  }

  .flow-block {
    overflow: hidden;
  }

  .wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 77px;

    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }

    .left-block {
      flex: 0 1 488px;
      width: 488px;

      @media (max-width: $tablet) {
        order: 2;
        flex: 0 100%;
        width: 100%;
        max-width: 455px;
        margin: 0 auto;
        text-align: center;
      }
      
      .main-title {
        font-size: 48px;
        text-align: left;
        line-height: 52px;
        margin-bottom: 32px;

        @media (max-width: $tablet) {
          margin-bottom: 24px;
          text-align: center;
        }

        @media (max-width: $mobile) {
          font-size: 44px;
          line-height: 44px;
        }
      }

      .description {
        display: flex;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #2E3F3A;
        margin-bottom: 40px;

        @media (max-width: $tablet) {
          margin-bottom: 32px;
        }
      }

      .button-group {
        display: flex;
        align-items: center;
        margin: 0 -8px;

        @media (max-width: $tablet) {
          justify-content: center;
        }

        @media (max-width: $mobile) {
          margin: 0 -4px;
        }

        .f_button {
          margin: 0 8px;

          @media (max-width: $mobile) {
            min-width: auto;
            flex: 0 calc(50% - 8px);
            width: calc(50% - 8px);
            margin: 0 4px;
          }
        }
      }
    }

    .right-block {
      flex: 0 calc(100% - 488px - 65px);
      width: calc(100% - 488px - 65px);
      margin-left: 65px;

      @media (max-width: $tablet) {
        order: 1;
        flex: 0 100%;
        width: 100%;
        margin: 0;
        display: flex;
        justify-content: center;
        margin-bottom: 60px;
      }

      @media (max-width: $mobile) {
        margin-bottom: 40px;
      }

      object {
        @media (max-width: $mobile) {
          width: 100%;
          max-width: 400px;
        }
      }
    }
  }

  .bottom-block {
    padding-bottom: 35px;

    @media (max-width: $tablet) {
      max-width: 584px;
      padding-bottom: 10px;
      margin: 0 auto;
    }

    @media (max-width: $mobile) {
      padding-bottom: 0;
      max-width: 370px;
    }

    .block-title {
      display: flex;
      justify-content: center;
      font-weight: 600;
      font-size: 24px;
      line-height: 32px;
      text-align: center;
      color: #042419;
      margin-bottom: 56px;

      @media (max-width: $tablet) {
        margin-bottom: 32px;
      }
    }

    .wrapper {
      margin: 0 -44px;
      justify-content: center;

      @media (max-width: $desk) {
        margin: 0 -24px;
      }

      @media (max-width: $tablet) {
        margin: -12px -44px;
      }

      @media (max-width: $mobile) {
        margin: -12px -17.5px;
        justify-content: flex-start;
      }

      .item {
        flex: 0 auto;
        margin: 0 44px;
        display: flex;
        align-items: center;
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: #2E3F3A;

        @media (max-width: $desk) {
          margin: 0 24px;
        }

        @media (max-width: $tablet) {
          margin: 12px 44px;
        }

        @media (max-width: $mobile) {
          flex: 0 calc(50% - 35px);
          width: calc(50% - 35px);
          margin: 12px 17.5px;
        }
        
        object {
          margin-right: 8px;
        }
      }
    }
  }

  .star {
    position: absolute;
    width: 8px;
    height: 8px;
    background: #F07CF2;
    border-radius: 10px;
    transition: all .5s;

    @media (max-width: $tablet) {
      transform: translate(0, 0) !important;
    }

    &.star-1 {
      top: 130px;
      left: 50%;

      @media (max-width: $tablet) {
        top: 170px;
        left: 180px;
      }

      @media (max-width: $mobile) {
        top: 170px;
        left: 25px;
      }
    }

    &.star-2 {
      top: 120px;
      right: 200px;

      @media (max-width: $tablet) {
        top: 130px;
        right: 70px;
      }

      @media (max-width: $mobile) {
        top: 90px;
        right: 46px;
      }
    }

    &.star-3 {
      left: 44px;
      top: 50%;

      @media (max-width: $tablet) {
        top: 400px;
        left: 44px;
      }

      @media (max-width: $mobile) {
        top: 600px;
        left: 11px;
      }
    }

    &.star-4 {
      top: 62%;
      left: 49%;

      @media (max-width: $tablet) {
        top: 516px;
        right: 33px;
        left: auto;
      }

      @media (max-width: $mobile) {
        top: 408px;
        right: 33px;
      }
    }
  }
}

.why-section {
  position: relative;
  padding: 160px 0 105px;
  overflow: hidden;

  @media (max-width: $tablet) {
    padding: 112px 0 80px;
  }

  @media (max-width: $mobile) {
    padding: 80px 0 48px;
  }

  .main-title {
    margin-bottom: 30px;

    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }

    @media (max-width: $mobile) {
      margin-bottom: 35px;
    }
  }

  .wrapper {
    display: flex;
    align-items: center;

    @media (max-width: $tablet) {
      display: block;
    }

    .left-block {
      flex: 0 calc(100% - 465px - 84px);
      width: calc(100% - 465px - 84px);
      margin-right: 84px;

      @media (max-width: $tablet) {
        width: 100%;
        margin: 0 0 64px;
        display: flex;
        justify-content: center;
      }

      @media (max-width: $mobile) {
        margin-bottom: 46px;
      }

      object {
        position: relative;
        margin-left: -84px;

        @media (max-width: $tablet) {
          margin: 0;
        }

        @media (max-width: $mobile) {
          width: 100%;
        }
      }
    }

    .right-block {
      flex: 0 1 465px;
      width: 465px;

      @media (max-width: $tablet) {
        margin: 0 auto;
      }

      @media (max-width: $mobile) {
        width: 100%;
        max-width: 465px;
      }

      p {
        margin: 20px 0 0;
        font-size: 16px;
        line-height: 24px;

        @media (max-width: $mobile) {
          text-align: center;
        }

        &:first-child {
          margin-top: 0;
        }
      }
    }
  }
}

.how-it-work-section {
  position: relative;
  padding: 105px 0 30px;
  overflow: hidden;

  @media (max-width: $tablet) {
    padding: 80px 0;
  }

  @media (max-width: $mobile) {
    padding: 48px 0;
  }

  .main-title {
    margin-bottom: 85px;

    @media (max-width: $tablet) {
      margin-bottom: 40px;
    }
  }

  .container {
    counter-reset: row-number;
  }

  .row-wrapper {
    display: flex;
    align-items: center;

    @media (max-width: $tablet) {
      flex-wrap: wrap;
      justify-content: center;
    }

    &:nth-child(2) {
      .img-block {
        flex: 0 calc(100% - 476px - 77px);
        margin-left: 77px;

        @media (max-width: $tablet) {
          order: 1;
          flex: 0 100%;
          width: 100%;
          margin: 0;
          display: flex;
          justify-content: center;
        }

        @media (max-width: $mobile) {
          flex: 0 calc(100% + 32px);
          width: calc(100% + 32px);
          max-width: 500px;
        }

        .img-wrapper {
          margin-right: -77px;

          @media (max-width: $tablet) {
            margin: 0 0 0 60px;
          }
        }
      }
    }

    &:nth-child(3) {
      margin-top: 87px;

      @media (max-width: $tablet) {
        margin-top: 70px;
      }

      .text-block {
        order: 2;
      }

      .img-block {
        order: 1;
        flex: 0 calc(100% - 476px - 61px);
        margin-right: 61px;
        margin-left: -61px;

        @media (max-width: $tablet) {
          width: 100%;
          flex: 0 100%;
          margin: 0 0 25px;
          display: flex;
          justify-content: center;
        }

        @media (max-width: $mobile) {
          flex: 0 calc(100% + 32px);
          width: calc(100% + 32px);
          max-width: 500px;
        }
      }
    }

    &:nth-child(4) {
      margin-top: 145px;

      @media (max-width: $tablet) {
        margin-top: 100px;
      }

      @media (max-width: $mobile) {
        margin-top: 80px;
      }

      .img-block {
        flex: 0 calc(100% - 476px - 88px);
        margin-left: 88px;
        margin-right: -88px;

        @media (max-width: $tablet) {
          order: 1;
          flex: 0 100%;
          width: 100%;
          margin: 0 0 30px;
          display: flex;
          justify-content: center;
        }

        @media (max-width: $mobile) {
          flex: 0 calc(100% + 32px);
          width: calc(100% + 32px);
          max-width: 500px;
        }

      }
    }

    .img-block {
      @media (max-width: $mobile) {
        margin: 0 -16px !important;
      }

      object {
        @media (max-width: $mobile) {
          width: 100%;
        }
      }
    }

    .text-block {
      flex: 0 1 476px;
      width: 476px;
      padding-left: 60px;

      @media (max-width: $tablet) {
        order: 2;
        text-align: center;
      }

      @media (max-width: $mobile) {
        padding: 0;
      }

      .block-title {
        position: relative;
        display: flex;
        font-weight: 600;
        font-size: 24px;
        line-height: 33px;
        color: #042419;
        margin-bottom: 24px;

        @media (max-width: $tablet) {
          justify-content: center;
          text-align: center;
          display: inline-flex;
        }

        @media (max-width: $mobile) {
          display: block;
        }

        &:before {
          position: absolute;
          top: 4px;
          left: -60px;
          counter-increment: row-number;
          content: "0" counter(row-number);
          font-weight: 600;
          font-size: 24px;
          line-height: 24px;
          color: #E2EBE8;

          @media (max-width: $mobile) {
            top: auto;
            left: 0;
            position: relative;
            line-height: 33px;
            margin-right: 10px;
          }

        }
      }

      p {
        margin: 0;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        
        @media (max-width: $tablet) {
          text-align: center;
        }
      }
    }

  }
}

.our-price-section {
  position: relative;
  padding: 80px 0 160px;
  overflow: hidden;

  @media (max-width: $mobile) {
    padding: 48px 0 96px;
  }

  .main-title {
    margin-bottom: 32px;

    @media (max-width: $mobile) {
      margin-bottom: 24px;
    }
  }

  .switcher-block {
    max-width: 292px;
    margin: 0 auto;
    border-radius: 100px;
    display: flex;
    align-items: center;
    border: 1px solid #E0F0EC;
    background: #F0F5F0;
    height: 56px;
    padding: 6px;
    position: relative;
    z-index: 1;
    transition: all .3s;

    &:hover {
      box-shadow: 0px 6px 12px rgba(4, 36, 25, 0.07);
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
      width: 1px;
      height: 1px;
      opacity: 0;
      appearance: none;

      &:checked {
        & + label {
          color: #2E3F3A;
          font-weight: 700;
        }
      }

      &.custom-rd.mn {
        &:checked {
          & ~ .switcher-bg {
            left: 5px;
          }
        }
      }

      &.custom-rd.yr {
        &:checked {
          & ~ .switcher-bg {
            left: 145px;
          }
        }
      }
    }

    label {
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #6B837C;
      flex: 0 50%;
      width: 50%;
      height: 100%;
      cursor: pointer;
      transition: all .3s;
      transition-delay: .1s;
    }

    .switcher-bg {
      position: absolute;
      top: 5px;
      //left: 5px;
      width: 140px;
      height: 44px;
      box-shadow: 0px 1px 3px rgba(16, 24, 40, 0.1), 0px 1px 2px rgba(16, 24, 40, 0.06);
      background: #fff;
      z-index: -1;
      border-radius: 100px;
      transition: all .5s;
    }
  }

  .slider {
    margin-top: 84px;
    display: flex;

    @media (max-width: $tablet) {
      display: block;
      max-width: 357px;
      margin: 56px auto 0;
    }

    @media (max-width: $mobile) {
      max-width: 328px;
    }
  }


  .swiper-slide {
    position: relative;
    padding: 32px;
    border: 1px solid #C2D2CE;
    border-radius: 16px;
    flex: 0 calc(33.333% - 16px);
    width: calc(33.333% - 16px);
    margin: 0 16px;

    @media (max-width: $tablet) {
      max-width: 357px;
      width: auto;
      margin: 64px 0 0;
    }

    @media (max-width: $mobile) {
      max-width: 328px;
      padding: 24px 14px;
    }

    &:first-child {
      @media (max-width: $tablet) {
        margin-top: 0;
      }
    }

    &:nth-child(2) {
      border-top: 0;
      border-radius: 0 0 16px 16px;

      @media (max-width: $tablet) {
        margin-top: 104px;
      }

      .popular {
        position: absolute;
        top: -44px;
        left: -1px;
        right: -1px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #F0F5F0;
        border: 1px solid #C2D2CE;
        border-bottom: 0;
        border-radius: 16px 16px 0 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        color: #042419;
      }
    }

    .icon {
      display: flex;
      justify-content: center;
      margin-bottom: 16px;
    }

    .item-title {
      display: flex;
      justify-content: center;
      font-weight: 700;
      font-size: 24px;
      line-height: 33px;
      text-align: center;
      color: #042419;
      margin-bottom: 6px;
    }

    .item-price {
      display: flex;
      justify-content: center;
      font-weight: 600;
      font-size: 20px;
      line-height: 30px;
      text-align: center;
      color: #2E3F3A;

      b {
        font-weight: 600;
      }

      i {
        font-style: normal;
      }
    }

    .item-list {
      @include reset-list;
      margin-top: 24px;

      li {
        position: relative;
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #2E3F3A;
        margin-top: 16px;
        padding-left: 36px;

        &.unchecked {
          opacity: .2;
        }

        &:before {
          position: absolute;
          top: 0;
          left: 0;
          content: "";
          background: url(../img/tk-check-icon.svg) 0 0 no-repeat;
          width: 24px;
          height: 24px;
        }

        &:first-child {
          margin-top: 0;
        }
      }
    }

    .f_button {
      width: 100%;
      margin-top: 40px;
    }
  }
}

.discover-section {
  position: relative;
  background: #E6FAF2;
  margin-top: 52px;

  @media (max-width: $mobile) {
    margin-top: 25px;
  }

  &:before {
    position: absolute;
    top: -50px;
    left: 0;
    right: 0;
    content: "";
    height: 52px;
    background: #E6FAF2 url(../img/tk-corner-top-bg.jpg) 50% 0 no-repeat;
    z-index: -1;
    -webkit-background-size: 100% auto;
    background-size: 100% auto;

    @media (max-width: $mobile) {
      top: -23px;
      height: 25px;
    }
  }
  
  .main-title {
    text-align: left;
  }

  .wrapper {
    display: flex;
    align-items: center;
    padding-bottom: 96px;
    padding-top: 67px;

    @media (max-width: $tablet) {
      padding-bottom: 40px;
      flex-wrap: wrap;
    }

    @media (max-width: $mobile) {
      padding: 40px 0 32px;
    }

