      margin-right: 112px;
      border-radius: 8px;

      @media (max-width: $tablet) {
        order: 2;
        width: 100%;
        flex: 0 100%;
        margin: 80px auto 0;
        max-width: 665px;
      }

      @media (max-width: $mobile) {
        margin-top: 48px;
      }
    }

    .text-block {
      flex: 0 1 463px;
      width: 463px;

      @media (max-width: $tablet) {
        text-align: center;
        margin: 0 auto;
      }

      @media (max-width: $mobile) {
        width: 100%;
        flex: 0 100%;
      }

      .main-title {
        margin-bottom: 32px;
        
        @media (max-width: $tablet) {
          text-align: center;
        }
      }

