        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #2E3F3A;
        margin-bottom: 40px;
      }

      .button-group {
        display: flex;
        align-items: center;
        margin: 0 -8px;

        @media (max-width: $tablet) {
          justify-content: center;
        }

        @media (max-width: $mobile) {
          margin: 0 -4px;
        }

        .f_button {
          margin: 0 8px;

          @media (max-width: $mobile) {
            min-width: auto;
            flex: 0 calc(50% - 8px);
            width: calc(50% - 8px);
            margin: 0 4px;
          }
        }
      }
    }
  }

  .star {
    position: absolute;
    width: 8px;
    height: 8px;
    background: #F07CF2;
    border-radius: 10px;
    transition: all .5s;
    transform: translate(0, 0) !important;

    &.star-1 {
      top: 30px;
      left: 40%;

      @media (max-width: $tablet) {
        top: 0;
        left: 24px;
      }

      @media (max-width: $mobile) {
        top: 20px;
        left: 18px;
      }
    }

    &.star-2 {
      top: 0;
      right: 90px;

      @media (max-width: $tablet) {
        top: 20px;
        right: 189px;
      }

      @media (max-width: $mobile) {
        top: 135px;
        right: 14px;
      }
    }

    &.star-3 {
      left: 50px;
      top: 60%;

      @media (max-width: $tablet) {
        top: 350px;
        left: 128px;
      }

      @media (max-width: $mobile) {
        top: 290px;
        left: 68px;
      }
    }

    &.star-4 {
      bottom: 58px;
      left: 42%;

      @media (max-width: $tablet) {
        bottom: auto;
        top: 250px;
        right: 58px;
        left: auto;
      }

      @media (max-width: $mobile) {
        top: auto;
        bottom: 12px;
        right: 36px;
      }
    }
  }
}