.footer {
  background: #E6FAF2;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 72px;
    border-top: 1px solid #2E3F37;

    @media (max-width: $tablet) {
      min-height: 88px;
    }

    @media (max-width: $mobile) {
      min-height: auto;
      padding: 24px 0;
      flex-wrap: wrap;
    }

    .copyright {
      display: inline-flex;
      font-size: 14px;
      line-height: 24px;
      color: #2E3F3A;

      @media (max-width: $mobile) {
        margin-top: 16px;
        order: 2;
      }
    }

    .semi-logo {
      position: relative;
      display: inline-flex;
      align-items: center;
      font-size: 14px;
      line-height: 24px;
      color: #2E3F3A;
      padding-right: 100px;
      background: url("../img/tk-footer-logo.svg") 100% 50% no-repeat;

      @media (max-width: $mobile) {
        order: 1;
      }
    }
  }
}